	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Ever since Java's inception, in 1995, people have been compiling languages that aren't Java to bytecode and deploying them on the JVM. Lately, we are seeing an explosion in JVM languages. This is partly, but not only, because of Java 7, the first JVM to ship with invokedynamic, which is a quantum leap in polyglot runtime implementation. This session explains why emerging language implementations are becoming more common and more feasible to implement on the JVM with satisfactory performance. It tells you how we got there and walks through the technology already in place (such as invokedynamic) and that which is in progress. The future for the JVM is bright indeed. Filmed at JAX London 2013.","meta":{"duration":"PT51M4S"},"presenter":{"name":null},"source":{"id":"OKJgMwTLmEY","provider":"youtube"},"slug":"marcus-lagergren-the-java-virtual-machine-is-over-the-polyglot-vm-is-here","tag":["JVM","Marcus Lagergreen","Java","invokedynamic","polyglot","bytecode","JAX London 2013"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/OKJgMwTLmEY/default.jpg","height":90,"width":120}},"title":"Marcus Lagergren: The Java Virtual Machine is Over - The Polyglot VM is here"}}]}},"pathContext":{"slug":"polyglot","tag":"polyglot"}}

